body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#tileCard{
    width: 150px;
    position: relative;
    margin-left: auto;
    margin-right: auto;
}

/* .tiles{
  margin-left: 1%;
  margin-right: 1%;
} */

.blue{
    background: rgb(50, 50, 109);
}
.eyeBall{
    width: 100px;
    height: 100px;
    margin-left: 1%;
    margin-right: 1%;
}
.menu{
    position: fixed;
    display: block;
    height: 80%;
    width: 100%;
}
.menuCard{
    position: relative;
    height: 90%;
    width: 500px;
}

button{
    opacity: 1;
    background: rgba(210, 180, 140, 0.336);
}
.scoreCard{
    width: 500px;
    height: 200px;
    position: relative;
    margin-left: auto;
    margin-right: auto;
    font-family: 'Monoton', cursive;
}

.cardTable{
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  margin-top: 5%;
  width: 800px;
}

.scoreDisplay{
  display: block;
  text-align: center;
  position: relative;
  margin-left: auto;
  margin-right: auto;
  width: 500px;
}

